// Form Element
.directorist-form-group {
    &.directorist-faq-group{
        margin-bottom: 1rem;
    }
    .directorist-form-element {
        height: 42px;
        padding: 15px 20px;
        font-size: 14px;
        font-weight: 400;
        border-radius: 5px;
        width: 100%;
        border: 1px solid #dbdaf7;

        &:focus {
            outline: none;
            box-shadow: 0 0;
        }
    }
}

.directorist-btn {
    display: inline-block;
    font-size: 15px;
    font-weight: 500;
    color: $dark;
    vertical-align: middle;
    text-align: center;
    user-select: none;
    cursor: pointer;
    padding: 0 26px;
    line-height: 2.95 !important;
    border-radius: 5px !important;
    background-color: #F2F3F5;
    border-color: transparent;
    text-decoration: none;
  
    &:focus {
      outline: 0 !important;
      text-decoration: none !important;
    }
}
  
// Button Type
.directorist-btn {
    &.directorist-btn-primary {
      background-color: $primary;
      color: #fff;
    }
  
    &.directorist-btn-secondary {
      background-color: $secondary;
      color: #fff;
    }
  
    &.directorist-btn-dark {
      background-color: $dark;
      color: #fff;
    }
  
    &.directorist-btn-success {
      background-color: $success;
      color: #fff;
    }
  
    &.directorist-btn-info {
      background-color: $info;
      color: #fff;
    }
  
    &.directorist-btn-warning {
      background-color: $warning;
      color: #fff;
    }
  
    &.directorist-btn-danger {
      background-color: $danger;
      color: #fff;
    }
}

// Common Classes
.directorist-flex {
    display: flex;
}

.directorist-align-center {
    align-items: center;
}

.directorist-justify-content-center {
    justify-content: center;
}

.directorist-justify-content-between {
    justify-content: space-between;
}

.directorist-justify-content-around {
    justify-content: space-around;
}

.directorist-justify-content-start {
    justify-content: flex-start;
}

.directorist-justify-content-end {
    justify-content: flex-end;
}

.directorist-form-group textarea, .directorist-form-group textarea.directorist-form-element {
    min-height: 120px;
}

/* Modal Core Styles */
.directorist-modal {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    left: 0;
    top: 0;
    z-index: -1;
    overflow: hidden;
    outline: 0;
}

.directorist-modal__dialog {
    position: relative;
    width: 500px;
    margin: 30px auto;
    transition: 0.3s ease;
    opacity: 0;
    visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: calc(100% - 5rem);
    pointer-events: none;
}

.directorist-modal__content{
    width: 100%;
    background: #fff;
    pointer-events: auto;
    border-radius: 3px;
    position: relative;
    .directorist-modal__header{
        position: relative;
        padding: 15px;
        border-bottom: 1px solid #ececec;
        .directorist-modal__header--title{
            font-size: 16px;
            font-weight: 600 !important;
            margin: 0;
        }
        .directorist-modal-close{
            position: absolute;
            right: 15px;
            top: 10px;
            line-height: 1.45;
            padding: 6px;
            i{
                color: #cd2653;
            }
        }
    }
    .directorist-modal__body{
        padding: 20px 25px;
    }
    .directorist-modal__footer{
        border-top: 1px solid #ececec;
        padding: 18px;
        .directorist-modal__action{
            display: flex;
            justify-content: flex-end;
            margin: -7.5px;
            button{
                text-decoration: none;
                margin: 7.5px !important;
            }
        }
    }
}
.directorist-modal__dialog.directorist-modal--lg {
    width: 800px;
}

.directorist-modal__dialog.directorist-modal--xl {
    width: 1140px;
}

.directorist-modal__dialog.directorist-modal--sm {
    width: 300px;
}

.directorist-modal.directorist-fade {
    transition: .3s ease;
    opacity: 1;
    visibility: visible;
    z-index: 9999;
}

.directorist-modal.directorist-fade:not(.directorist-show) {
    opacity: 0;
    visibility: hidden;
}

.directorist-modal.directorist-show .directorist-modal__dialog {
    opacity: 1;
    visibility: visible;
    transition: 0.3s ease;
}

/* Responsive CSS */

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
    .at-modal-content.at-modal-xl,
    .at-modal-content.at-modal-lg,
    .at-modal-content.at-modal-md,
    .at-modal-content.at-modal-sm{
        width: calc(100% - 60px);
    }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
    .at-modal-content.at-modal-xl,
    .at-modal-content.at-modal-lg,
    .at-modal-content.at-modal-md,
    .at-modal-content.at-modal-sm{
        width: calc(100% - 60px);
    }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
    .at-modal-content.at-modal-xl,
    .at-modal-content.at-modal-lg,
    .at-modal-content.at-modal-md,
    .at-modal-content.at-modal-sm{
        width: calc(100% - 60px);
    }
}

/* Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
    .at-modal-content.at-modal-xl,
    .at-modal-content.at-modal-lg,
    .at-modal-content.at-modal-md,
    .at-modal-content.at-modal-sm{
        width: calc(100% - 30px);
    }
}

